.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 40px;
  width: 100%;
  height: 40px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  background-color: #68b738;
  border: 0;
  padding: 0;
  display: block;
  cursor: pointer;

  &:hover {
    background: #5eaa2f;
    transition: 0.25s ease-in-out;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
    transition: 0.25s ease-in-out;
  }

  &--light {
    color: #444444;
    background-color: #f2f2f2;

    &:hover {
      background: #ebebeb;
      transition: 0.25s ease-in-out;
    }

    &:active {
      color: rgba(17, 17, 17, 0.3);
      transition: 0.25s ease-in-out;
    }
  }
}
